<template>
  <div class="center">
    <div class="center_breadcrumb">
         <Crumb @headInfo="headInfo"/>
    </div>
    <div class="center_content">
        <div class="center_content_title">
            <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div>
        <div v-if="isAdd" class="defaultPage">
            <img src="@/../static/img/studentLearning.png" alt="">
            <el-button @click="create" type="primary" icon="el-icon-plus" class="idea-add" round>新建</el-button>
        </div>
        <div v-if="!isAdd" class="table-container">
             <div class="idea-title">
                <p v-text="data.title"></p>
                <span  @click="edit" class="idea-edit">
                    <SvgIcon icon-class="icon-edit-line" size="15px"></SvgIcon>
                    <el-button class="idea-edit--btn" type="text" size="small"> 编辑</el-button>
                </span>             
            </div>
           <div class="idea-introduce" v-html="data.content"></div>
        </div>
    </div>
 </div>
</template>

<script>
import trainingProgramApi from '@/api/trainingProgramApi';
export default {
 name: 'Idea',
 data() {
 return {
    pageTitle:'认证理念',
    pageTitleArr:[],
    isAdd:true,
    data:{}
 }
 },
 created(){
 },
 mounted(){
     this.initData()
 },
 methods:{
     initData(){
         const url =trainingProgramApi.conceptInfo
         this.$get(url).then(res =>{
             if(res.status === 1){
                 if(res.data){
                    this.isAdd=false
                    this.data=res.data
                 }else{
                   this.isAdd=true
                 }             
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     },
    create(){
         this.$router.push({
            path: "/trainingProgram/ideaCreate",
            query: {
                type: 'add',
                pageTitle:this.pageTitleArr
            }
        });
     },
    edit(){
        this.$router.push({
            path: "/trainingProgram/ideaCreate",
            query: {
                type: 'edit',
                pageTitle:this.pageTitleArr
            }
        });
     },
    headInfo(item){
        this.pageTitleArr=item
        this.pageTitle=item[1]
     },
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
  .idea-title{
      font-size:20px;
      color:#2c3e50;
      font-weight:700;
      line-height:50px;
      border-bottom:1px solid rgba(0, 0, 0, 0.1);
      margin-bottom:20px;
      position:relative;
      .idea-edit{
          display:inline-block;
          width: 60px;
          height: 40px;
          position: absolute;
          right: 0;
          top: 0;
          cursor:pointer;
      }
  }
  .idea-introduce{
      font-size:15px;
      color:#666;
      line-height: 30px;
  }
</style>



